import { axios } from '.';

export const getAllGerechten = async () => {
  const { data } = await axios.get('gerechten');
  return data;
};

export const saveGerecht = async ({ GerechtID, Naam, Prijs }) => {
  const { data } = await axios({
    method: 'GerechtID' ? 'put' : 'post',
    url: `gerechten/${GerechtID ?? ''}`,
    data: {
      GerechtID,
      Naam,
      Prijs,
    },
  });
  return data;
};

export const getGerechtById = async (id) => {
  const { data } = await axios.get(`gerechten/${id}`);
  return data;
};
