import React from 'react';
import { Routes, Route } from 'react-router-dom';

import MenuBar from './components/MenuBar';
import Klanten from './components/Klanten';
import Over from './components/Over';
import Facturatie from './components/Facturatie';
import Bestellingen from './components/Bestellingen';
import Gerechten from './components/Gerechten';
import LoginForm from './components/LoginForm';
import RequireAuth from './components/RequireAuth';

import { KlantenProvider } from './contexts/KlantenProvider';
import { AuthProvider } from './contexts/AuthProvider';
import { GerechtenProvider } from './contexts/GerechtenProvider';
import { BestellingenProvider } from './contexts/BestellingenProvider';

function App() {
  return (
    <AuthProvider>
      <KlantenProvider>
        <GerechtenProvider>
          <BestellingenProvider>
            <MenuBar />
            <Routes>
              <Route path="/login" element={<LoginForm />} />
              <Route
                path="/klanten"
                element={
                  <RequireAuth>
                    <Klanten />
                  </RequireAuth>
                }
              />

              <Route
                path="/over"
                element={
                  <RequireAuth>
                    <Over />
                  </RequireAuth>
                }
              />
              <Route
                path="/facturatie"
                element={
                  <RequireAuth>
                    <Facturatie />
                  </RequireAuth>
                }
              />
              <Route
                path="/bestellingen"
                element={
                  <RequireAuth>
                    <Bestellingen />
                  </RequireAuth>
                }
              />
              <Route
                path="/gerechten"
                element={
                  <RequireAuth>
                    <Gerechten />
                  </RequireAuth>
                }
              />
            </Routes>
          </BestellingenProvider>
        </GerechtenProvider>
      </KlantenProvider>
    </AuthProvider>
  );
}

export default App;
