import { Button } from '@blueprintjs/core';
import { DateRangePicker } from '@blueprintjs/datetime';
import { BestellingenContext } from '../contexts/BestellingenProvider';
import BestellingenForm from './BestellingenForm';
import React, { useContext, useEffect, useState } from 'react';
import Spreadsheet from './Spreadsheet';
import ErrorDisplay from './ErrorDisplay';

const vandaag = new Date();
const volgendeWeek = new Date();
const volgendJaar = new Date();
volgendeWeek.setDate(volgendeWeek.getDate() + 7);
volgendJaar.setDate(vandaag.getDate() + 365);

export default function Bestellingen() {
  const {
    bestellingen,
    error,
    loading,
    // createOrUpdateBestelling,
    viewPortWijzigen,
  } = useContext(BestellingenContext);

  const [beginDatum, setBeginDatum] = useState(vandaag);
  const [eindDatum, setEindDatum] = useState(volgendeWeek);
  const [getoondeData, setGetoondeData] = useState(bestellingen);
  const [geselecteerdeBestelling, setGeselecteerdeBestelling] = useState([]);

  const setDatumVorigeWeek = () => {
    let tempStart = new Date();
    let tempEind = new Date();
    tempStart.setDate(tempStart.getDate() - 14);
    if (tempStart.getDay() === 1) {
      tempStart.setDate(tempStart.getDate() + 7);
    } else {
      do {
        tempStart.setDate(tempStart.getDate() + 1);
      } while (tempStart.getDay() !== 1);
    }
    tempEind.setDate(tempStart.getDate() + 7);
    setBeginDatum(tempStart);
    setEindDatum(tempEind);
  };

  const setDatumDezeWeek = () => {
    let tempStart = new Date();
    let tempEind = new Date();
    while (tempStart.getDay() !== 1) {
      tempStart.setDate(tempStart.getDate() - 1);
    }
    tempEind.setDate(tempEind.getDate() + 7);
    setBeginDatum(tempStart);
    setEindDatum(tempEind);
  };

  const setDatumVolgendeWeek = () => {
    let tempStart = new Date();
    let tempEind = new Date();
    if (tempStart.getDay() === 1) {
      tempStart.setDate(tempStart.getDate() + 7);
    } else {
      while (tempStart.getDay() !== 1) {
        tempStart.setDate(tempStart.getDate() + 1);
      }
    }
    tempEind.setDate(tempStart.getDate() + 7);
    setBeginDatum(tempStart);
    setEindDatum(tempEind);
  };

  const pasViewportAan = ([beginDatum, eindDatum]) => {
    setBeginDatum(beginDatum);
    setEindDatum(eindDatum);
  };

  useEffect(() => {
    if (
      beginDatum < eindDatum &&
      beginDatum > new Date('2000-01-01') &&
      eindDatum > new Date('2000-01-01')
    ) {
      viewPortWijzigen(beginDatum, eindDatum);
      setGeselecteerdeBestelling(null);
    }
  }, [beginDatum, eindDatum]);

  useEffect(() => {
    setGetoondeData(
      bestellingen.filter((b) => {
        const d = new Date(b.datum);
        if (d >= beginDatum && d <= eindDatum) return true;
        return false;
      })
    );
  }, [loading, bestellingen, beginDatum, eindDatum]);

  // useEffect(() => {
  //   bestellingen = { ...bestellingen, Naam: klantNaam, Gerecht: gerechtNaam };
  // }, [bestellingen]);

  if (loading) return <div>Loading</div>;
  if (error) return <ErrorDisplay errortext={error} provider="bestellingen" />;

  const KOLOMMEN = [
    'datum',
    'betaald',
    'adres',
    'gemeente',
    'gerechtNaam',
    'klantNaam',
  ];
  const KOLOMBREEDTES = [90, 70, 130, 100, 80, 90, 145];

  return (
    <section id="bestellingen">
      <div style={{ width: '60%', display: 'inline-block' }}>
        <DateRangePicker
          value={[beginDatum, eindDatum]}
          onChange={(range) => pasViewportAan(range)}
          shortcuts={false}
          maxDate={volgendJaar}
        />
        <Button onClick={setDatumVorigeWeek}>Vorige week</Button>
        <Button onClick={setDatumDezeWeek}>Deze week</Button>
        <Button onClick={setDatumVolgendeWeek}>Volgende week</Button>

        <Spreadsheet
          data={getoondeData}
          kolommen={KOLOMMEN}
          onRowChange={null} // we gaan niet wijzigen in de tabel zelf
          filter={''} // we gaan niet filteren
          filterKolommen={['klantNaam']}
          kolombreedtes={null}
          tableEditable={false}
          onSelectedChange={setGeselecteerdeBestelling}
        />

        {/* {bestellingen.map((p) => (
        <p>${JSON.stringify(p)}</p>
      ))} */}
      </div>
      <div
        style={{ width: '40%', display: 'inline-block', verticalAlign: 'top' }}
      >
        {geselecteerdeBestelling && (
          <BestellingenForm bestelling={geselecteerdeBestelling} />
        )}
      </div>
    </section>
  );
}
