import { Navbar, Button, Alignment } from '@blueprintjs/core';
import { useLocation, useNavigate } from 'react-router-dom';
import { useSession, useLogout } from '../contexts/AuthProvider';

function NavigatieKnop(params) {
  const { link, icon, text } = params;
  const location = useLocation();
  const navigate = useNavigate();
  const logout = useLogout();
  const style =
    location.pathname === link ? 'bp3-intent-primary' : 'bp3-minimal';
  return (
    <Button
      className={style}
      icon={icon}
      text={text}
      onClick={
        link === '/logout'
          ? () => {
              logout();
            }
          : () => navigate(link)
      }
    />
  );
}

export default function MenuBar() {
  const { isAuthed, user } = useSession();
  return (
    <nav id="hoofdmenu">
      <Navbar>
        <Navbar.Group align={Alignment.LEFT}>
          <NavigatieKnop link="/klanten" icon="people" text="Klanten" />
          <NavigatieKnop link="/bestellingen" icon="th" text="Bestellingen" />
          <NavigatieKnop link="/gerechten" icon="glass" text="Gerechten" />
          <NavigatieKnop link="/facturatie" icon="euro" text="Facturatie" />
          <NavigatieKnop link="/over" icon="lightbulb" text="Over" />
          {/* <NavigatieKnop link="/test" icon="lightbulb" text="Test" /> */}
        </Navbar.Group>
        <Navbar.Group align={Alignment.RIGHT}>
          <Navbar.Divider />
          {isAuthed ? (
            <NavigatieKnop link="/logout" icon="lightbulb" text="Afmelden" />
          ) : (
            <NavigatieKnop link="/login" icon="lightbulb" text="Aanmelden" />
          )}

          <Navbar.Heading>
            <img
              src="https://www.traiteurcarl.be/smarty/wireframe07/media/images/logo-Traiteur-Carl.png"
              alt="logo"
              style={{ height: '55px' }}
            />

            {/* <p style={{ height: '0.5em', color: 'olive' }}>Traiteur Carl</p>
            <p style={{ height: '0.5em', color: 'olive' }}>App</p> */}
          </Navbar.Heading>
        </Navbar.Group>
      </Navbar>
    </nav>
  );
}
