import { axios } from '.';

export const getAllKlanten = async () => {
  const { data } = await axios.get('klanten');
  console.log('Got data');
  console.dir(data);
  return data;
};

export const saveKlant = async ({
  KlantID,
  Naam,
  Voornaam,
  Straat,
  Huisnummer,
  Postcode,
  Gemeente,
  Telefoonnummer,
  GSM,
  Email,
  OpenstaandSaldo,
  VasteKlant,
}) => {
  let method = 'put';
  if (KlantID === undefined) {
    method = 'post';
  }
  console.log(`KlantID: ${KlantID}`);
  console.log(`Method: ${method}`);
  const { data } = await axios({
    method: method,
    url: `klanten/${KlantID ?? ''}`,
    data: {
      KlantID,
      Voornaam,
      Naam,
      Straat,
      Huisnummer,
      Postcode,
      Gemeente,
      Telefoonnummer,
      GSM,
      Email,
      OpenstaandSaldo,
      VasteKlant,
    },
  });
  return data;
};

export const disableKlant = async (KlantID) => {};
