import {
  createContext,
  useState,
  useEffect,
  useCallback,
  useContext,
  useMemo,
} from 'react';
import * as gerechtenApi from '../api/gerechten';
import { useSession } from './AuthProvider';

export const GerechtenContext = createContext();
export const useGerechten = () => useContext(GerechtenContext);

export const GerechtenProvider = ({ children }) => {
  const { ready: authReady } = useSession();
  const [initialLoad, setInitialLoad] = useState(false);
  const [currentGerecht, setCurrentGerecht] = useState({});
  const [error, setError] = useState();
  const [loading, setLoading] = useState(false);
  const [gerechten, setGerechten] = useState([]);

  const clearError = useCallback(async () => {
    setError(null);
    refreshGerechten();
  }, []);

  const refreshGerechten = useCallback(async () => {
    try {
      setError();
      setLoading(true);
      const data = await gerechtenApi.getAllGerechten();
      setGerechten(data.data);
    } catch (error) {
      console.log(error);
      setError(error);
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    if (authReady && !initialLoad) {
      refreshGerechten();
      setInitialLoad(true);
    }
  }, [authReady, initialLoad, refreshGerechten]);

  const createOrUpdateGerecht = useCallback(
    async ({ GerechtID, Naam, Prijs }) => {
      setError();
      setLoading(true);
      try {
        const changedGerecht = await gerechtenApi.saveGerecht({
          GerechtID,
          Naam,
          Prijs,
        });
        await refreshGerechten();
        return changedGerecht;
      } catch (error) {
        console.error(error);
        setError(error);
        throw error;
      } finally {
        setLoading(false);
      }
    },
    [refreshGerechten]
  );

  const value = useMemo(
    () => ({
      gerechten,
      error,
      clearError,
      loading,
      currentGerecht,
      setCurrentGerecht,
      createOrUpdateGerecht,
    }),
    [
      gerechten,
      error,
      clearError,
      loading,
      setCurrentGerecht,
      currentGerecht,
      createOrUpdateGerecht,
    ]
  );

  return (
    <GerechtenContext.Provider value={value}>
      {children}
    </GerechtenContext.Provider>
  );
};
