import {
  Classes,
  Dialog,
  FormGroup,
  InputGroup,
  ControlGroup,
  Button,
} from '@blueprintjs/core';
import { Tooltip2 } from '@blueprintjs/popover2';
import React, { useState } from 'react';
import { useNavigate } from 'react-router';
import { useLogin } from '../contexts/AuthProvider';

// import FormsyText from 'formsy-blueprintjs/lib/formsyTextField';

// import Formsy from 'formsy-react';
// import MyInput from './MyInput';

const LoginForm = () => {
  const navigate = useNavigate();

  const login = useLogin();
  const valideerFormulier = () => true;
  const [email, setEmail] = useState();
  const [password, setPassword] = useState();
  const [showPassword, setShowPassword] = useState(false);
  const [foutmelding, setFoutmelding] = useState(false);

  const handleLogin = async () => {
    const success = await login(email, password);
    if (success) {
      navigate('/');
    } else {
      setFoutmelding(true);
    }
  };

  const lockButton = (
    <Tooltip2 content={`${showPassword ? 'Toon' : 'Verberg'} Wachtwoord`}>
      <Button
        icon={showPassword ? 'unlock' : 'lock'}
        minimal={true}
        onClick={() => setShowPassword(!showPassword)}
      />
    </Tooltip2>
  );

  return (
    <Dialog
      title="Aanmelden"
      icon="tag"
      isOpen={true}
      width={500}
      canEscapeKeyClose={false}
      canOutsideClickClose={false}
      isCloseButtonShown={false}
    >
      <div className={Classes.DIALOG_BODY}>
        {foutmelding ? (
          <div style={{ color: 'red' }}>
            <p>FOUT: Gebruiker en/of wachtwoord niet correct</p>
          </div>
        ) : null}
        <FormGroup label="Email" labelFor="text-input" labelInfo="(verplicht)">
          <ControlGroup>
            <InputGroup
              id="text-input"
              placeholder="Email"
              onChange={(e) => {
                setEmail(e.target.value);
                setFoutmelding(false);
              }}
              style={{ width: '420px' }}
            />
          </ControlGroup>
        </FormGroup>
        <FormGroup
          label="Wachtwoord"
          labelFor="text-input"
          labelInfo="(verplicht)"
        >
          <ControlGroup>
            <InputGroup
              id="text-input"
              placeholder="Wachtwoord"
              onChange={(e) => {
                setPassword(e.target.value);
                setFoutmelding(false);
              }}
              rightElement={lockButton}
              type={showPassword ? 'text' : 'password'}
              style={{ width: '420px' }}
            />
          </ControlGroup>
        </FormGroup>

        <Button
          onClick={() => {
            valideerFormulier()
              ? handleLogin()
              : console.log('Niet voldoende info');
          }}
          type="submit"
          text="Aanmelden"
          icon="follower"
        />
      </div>
    </Dialog>
  );
};

export default LoginForm;
