import {
  Column,
  Table2,
  EditableCell2,
  Cell,
  TruncatedFormat,
  RegionCardinality,
} from '@blueprintjs/table';
import { useEffect, useState } from 'react';

const Spreadsheet = ({
  data, // de objecten om in de spreadsheet te tonen
  kolommen, // de attributen van de objecten die een kolom krijgen (de rest wordt niet getoond)
  onRowChange, // functie die uitegevoerd moet worden als data gewijzigd werd. Geeft volledig gewijzigd object terug als parameter.
  filter, // filtertekst
  filterKolommen, // de kolommen(attributen) die gecheckt worden op de filtertekst
  kolombreedtes, // array met de breedtes van alle kolommen.
  tableEditable, // kan de tabel op dit moment gewijzigd worden of niet?
  onSelectedChange = () => {}, // functie die aangeroepen wordt als we van cel wijzigen
}) => {
  const [dataKopie, setDataKopie] = useState([]); // dataKopie: bevat de data + extra kolom "filtered" voor filtering

  useEffect(() => {
    const matchKolommen = (x) => {
      return filterKolommen.reduce((gevonden, kolom) => {
        const celwaarde = x[`${kolom}`];
        if (celwaarde === null) return gevonden;
        if (celwaarde.startsWith(filter)) return true;
        if (celwaarde.toLowerCase().startsWith(filter.toLowerCase()))
          return true;
        return gevonden;
      }, false);
    };

    setDataKopie(data.filter((x) => matchKolommen(x)));
  }, [data, filter, filterKolommen]);

  const celRender = (i, j) => {
    return tableEditable ? (
      <EditableCell2
        value={dataKopie[i][kolommen[j]]}
        onConfirm={(value) => {
          const nieuweData = { ...dataKopie[i], [`${kolommen[j]}`]: value };
          onRowChange(nieuweData);
          dataKopie[i] = nieuweData;
        }}
      >
        <TruncatedFormat />
      </EditableCell2>
    ) : (
      <Cell>
        <TruncatedFormat>
          {dataKopie[i][kolommen[j]] === null ? '' : dataKopie[i][kolommen[j]]}
        </TruncatedFormat>
      </Cell>
    );
  };

  return (
    <Table2
      numRows={dataKopie.length}
      columnWidths={kolombreedtes}
      enableColumnResizing={false}
      selectionModes={[RegionCardinality.CELLS]}
      enableMultipleSelection={false} // er mag maar één cel geselecteerd worden (voor de callback hieronder)
      onSelection={(e) => {
        if (e && e[0] !== undefined && e[0].rows[0] !== undefined) {
          const geselecteerdeRij = e[0].rows[0];
          const geselecteerdeData = dataKopie[geselecteerdeRij];
          onSelectedChange(geselecteerdeData);
        }
      }}
    >
      {kolommen.map((i, index) => (
        <Column key={index} name={i} cellRenderer={celRender} />
      ))}
    </Table2>
  );
};

export default Spreadsheet;
