import {
  createContext,
  useState,
  useEffect,
  useCallback,
  useContext,
  useMemo,
} from 'react';
import * as bestellingenApi from '../api/bestellingen';
import { useSession } from './AuthProvider';

export const BestellingenContext = createContext();
export const useBestellingen = () => useContext(BestellingenContext);

export const BestellingenProvider = ({ children }) => {
  const { ready: authReady } = useSession();
  const [initialLoad, setInitialLoad] = useState(false);
  const [currentBestelling, setCurrentBestelling] = useState({});
  const [error, setError] = useState();
  const [loading, setLoading] = useState(false);
  const [bestellingen, setBestellingen] = useState([]);
  const [cache, setCache] = useState([]);

  const clearError = useCallback(async () => {
    setError(null);
    let startDatum = new Date();
    let eindDatum = new Date().setDate(startDatum().getDate() - 7);
    refreshBestellingen(startDatum, eindDatum, cache); // TODO: welke bestellingen opnieuw ophalen van de server?
  }, []);

  const refreshBestellingen = useCallback(
    async (startDatum, eindDatum, cache) => {
      try {
        setError();
        setLoading(true);

        let teWijzigenData = cache;

        for (let i = teWijzigenData.length - 1; i >= 0; i--) {
          const testDatum = new Date(teWijzigenData[i].datum);
          console.log(testDatum);
          if (testDatum >= startDatum && testDatum <= eindDatum) {
            teWijzigenData.splice(i, 1);
          }
        }

        for (
          let dag = new Date(startDatum);
          dag <= eindDatum;
          dag.setDate(dag.getDate() + 1)
        ) {
          const data = await bestellingenApi.getBestellingenByDag(dag);
          data.data.map((e) => {
            teWijzigenData.push(e);
            return null;
          });
          console.log(teWijzigenData);
        }
        setBestellingen(teWijzigenData);
        setCache(teWijzigenData);
        console.log(teWijzigenData);
      } catch (error) {
        console.log(error);
        setError(error);
      } finally {
        setLoading(false);
      }
    },
    []
  );

  useEffect(() => {
    if (authReady && !initialLoad) {
      const vandaag = new Date();
      vandaag.setDate(vandaag.getDate() - 1);
      let volgendeWeek = new Date(vandaag);
      volgendeWeek.setDate(vandaag.getDate() + 7);
      refreshBestellingen(vandaag, volgendeWeek, []);
      setInitialLoad(true);
    }
  }, [authReady, initialLoad, refreshBestellingen]);

  const createOrUpdateBestelling = useCallback(
    async ({ BestellingID, datum, klantID, gerechtID, aantal, betaald }) => {
      setError();
      setLoading(true);
      try {
        const changedGerecht = await bestellingenApi.saveBestelling({
          BestellingID,
          datum,
          klantID,
          gerechtID,
          aantal,
          betaald,
        });
        let teFresreshenDatum = new Date(datum);
        await refreshBestellingen(
          teFresreshenDatum,
          teFresreshenDatum.setDate(teFresreshenDatum.getDate() + 1)
        );
        return changedGerecht;
      } catch (error) {
        console.error(error);
        setError(error);
        throw error;
      } finally {
        setLoading(false);
      }
    },
    [refreshBestellingen]
  );

  const viewPortWijzigen = useCallback(
    async (begindatum, einddatum) => {
      await refreshBestellingen(
        begindatum,
        einddatum,
        cache.length > 1 ? cache : []
      );
    },
    [refreshBestellingen, cache]
  );

  const value = useMemo(
    () => ({
      bestellingen,
      error,
      clearError,
      loading,
      currentBestelling,
      setCurrentBestelling,
      createOrUpdateBestelling,
      viewPortWijzigen,
    }),
    [
      bestellingen,
      error,
      clearError,
      loading,
      currentBestelling,
      setCurrentBestelling,
      createOrUpdateBestelling,
      viewPortWijzigen,
    ]
  );

  return (
    <BestellingenContext.Provider value={value}>
      {children}
    </BestellingenContext.Provider>
  );
};
