import { useState } from 'react';

import { Classes, Dialog, Switch } from '@blueprintjs/core';

import Formsy from 'formsy-react';
import StraatNaamInput from './formsyFields/StraatnaamInput';
import HuisnummerInput from './formsyFields/HuisnummerInput';
import GemeenteInput from './formsyFields/GemeenteInput';
import NaamInput from './formsyFields/NaamInput';
import VoornaamInput from './formsyFields/VoornaamInput.js';
import PostcodeInput from './formsyFields/PostcodeInput';
import EmailInput from './formsyFields/EmailInput';
import TelefoonnummerInput from './formsyFields/TelefoonnummerInput';
import GSMInput from './formsyFields/GSMInput';

const KlantToevoegen = ({ onClose, onSubmit }) => {
  const [vasteKlantCheckbox, setVasteKlantCheckbox] = useState(false);
  const [submitKnopDisabled, setSubmitKnopDisabled] = useState(false);

  return (
    <Dialog
      title="Klant Toevoegen"
      icon="tag"
      isOpen={true}
      onClose={onClose}
      width={500}
      canEscapeKeyClose={false}
      canOutsideClickClose={false}
      style={{ width: '600px' }}
    >
      <div className={Classes.DIALOG_BODY}>
        <Formsy
          onValidSubmit={(e) => {
            e.vasteKlant = vasteKlantCheckbox;
            console.log(e);
            onSubmit({
              Voornaam: e.Voornaam,
              Naam: e.Naam,
              Straat: e.Straat,
              Huisnummer: e.Huisnummer,
              GSM: e.GSM,
              Postcode: e.Postcode,
              Gemeente: e.Gemeente,
              Telefoonnummer: e.Telefoonnummer,
              Email: e.Email,
              vasteKlant: e.vasteKlant,
              OpenstaandSaldo: 0,
            });
          }}
          onValid={() => setSubmitKnopDisabled(false)}
          onInvalid={() => setSubmitKnopDisabled(true)}
        >
          <div style={{ padding: '0.3em' }}>
            <VoornaamInput name="Voornaam" />
          </div>
          <div style={{ padding: '0.3em' }}>
            <NaamInput name="Naam" />
          </div>

          <hr />

          <div style={{ padding: '0.3em' }}>
            <StraatNaamInput
              required
              name="Straat"
              validations={{
                minLength: 1,
              }}
              validationErrors={{
                minLength: 'Moet minstens 1 karakter lang zijn',
              }}
            />
          </div>

          <div style={{ padding: '0.3em' }}>
            <HuisnummerInput
              required
              name="Huisnummer"
              //eslint-
              validations={{
                minLength: 1,
                matchRegexp: /^[0-9]+[a-zA-Z]?$/, //string moet beginnen met cijfer, mag eindigen met nul of één cijfers
              }}
              validationErrors={{
                minLength: 'Huisnummer is verplicht',
                matchRegexp: 'Huisnummer is niet geldig',
              }}
            />
          </div>

          <div style={{ padding: '0.3em' }}>
            <GemeenteInput name="Gemeente" />
          </div>

          <div style={{ padding: '0.3em' }}>
            <PostcodeInput
              name="Postcode"
              validations={{
                minLength: 1,
                maxLength: 6,
                isInt: true,
              }}
              validationErrors={{
                minLength: 'Moet minstens 1 cijfer bevatten',
                maxLength: 'Mag maximaal 6 cijfers bevatten',
                isInt: 'Mag enkel uit cijfers bestaan',
              }}
            />
          </div>

          <hr />

          <div style={{ padding: '0.3em' }}>
            <EmailInput
              name="Email"
              validations={{ isEmail: true }}
              validationErrors={{ isEmail: 'Ongeldig emailadres' }}
            />
          </div>

          <div style={{ padding: '0.3em' }}>
            <TelefoonnummerInput
              name="Telefoonnummer"
              validations={{
                matchRegexp:
                  /^((\+|00)32\s?|0)(\d\s?\d{3}|\d{2}\s?\d{2})(\s?\d{2}){2}$/,
              }}
              validationErrors={{
                matchRegexp: 'Ongeldig telefoonnummer',
              }}
            />
          </div>

          <div style={{ padding: '0.3em' }}>
            <GSMInput
              name="GSM"
              validations={{
                matchRegexp: /^((\+|00)32\s?|0)4(60|[789]\d)(\s?\d{2}){3}$/,
              }}
              validationErrors={{
                matchRegexp: 'Ongeldig GSM nummer',
              }}
            />
          </div>

          <Switch
            checked={vasteKlantCheckbox}
            label="Vaste Klant"
            onChange={() => setVasteKlantCheckbox(!vasteKlantCheckbox)}
          />

          <button type="submit" disabled={submitKnopDisabled}>
            Klant Toevoegen
          </button>
        </Formsy>
      </div>
    </Dialog>
  );
};

export default KlantToevoegen;
