import { Classes, Dialog, Button } from '@blueprintjs/core';
import { KlantenContext } from '../contexts/KlantenProvider';
import { BestellingenContext } from '../contexts/BestellingenProvider';
import { useContext } from 'react';
import { GerechtenContext } from '../contexts/GerechtenProvider';

const ErrorDisplay = (errortext, provider) => {
  const { clearError: clearKlantenError } = useContext(KlantenContext);
  const { clearError: clearBestellingenError } =
    useContext(BestellingenContext);
  const { clearError: clearGerechtenError } = useContext(GerechtenContext);

  console.log(errortext);

  return (
    <Dialog
      title="FOUT"
      icon="error"
      isOpen={true}
      width={500}
      canEscapeKeyClose={true}
      canOutsideClickClose={true}
      isCloseButtonShown={true}
    >
      <div className={Classes.DIALOG_BODY}>
        <p>{errortext.errortext.response.statusText}</p>
        <p>{errortext.errortext.message}</p>
      </div>
      <Button
        onClick={() => {
          if (provider === 'klanten') {
            clearKlantenError();
          }
          if (provider === 'bestellingen') {
            clearBestellingenError();
          }
          if (provider === 'gerechten') {
            clearGerechtenError();
          }
        }}
      >
        OK
      </Button>
    </Dialog>
  );
};

export default ErrorDisplay;
