import { KlantenContext } from '../contexts/KlantenProvider';
import React, { useContext, useState } from 'react';
import {
  ControlGroup,
  InputGroup,
  HTMLSelect,
  FocusStyleManager,
  Checkbox,
  Button,
  Dialog,
} from '@blueprintjs/core';
import Spreadsheet from './Spreadsheet';
import KlantToevoegen from './KlantToevoegen';
import ErrorDisplay from './ErrorDisplay';

const Klanten = () => {
  const DEFAULTFILTERKOLOMMEN = ['Voornaam', 'Naam', 'Telefoonnummer', 'GSM'];
  const { klanten, error, errorType, loading, createOrUpdateKlant } =
    useContext(KlantenContext);

  const [tableEditable, setTableEditable] = useState(false); // of de tabel gewijzigd mag worden of niet. beveiliging tegen onbedoeld overschrijven.
  const [filter, setFilter] = useState('');
  const [filterKolommen, setFilterKolommen] = useState(DEFAULTFILTERKOLOMMEN); // default matchen op deze KOLOMMEN]
  const [toevoegenFormulier, setToevoegenFormulier] = useState(false);

  FocusStyleManager.onlyShowFocusOnTabs(); // -> Om te voorkomen dat een blauwe outline getoond wordt als het component actief is.
  // prettier-ignore
  const KOLOMMEN = [ "Voornaam", "Naam", "Straat", "Huisnummer", "Postcode", "Gemeente", "Telefoonnummer", "GSM", "Email", "OpenstaandSaldo" ];
  const KOLOMBREEDTES = [150, 150, 150, 100, 100, 150, 125, 150, 250, 110];

  // TODO: Echte validatie uitvoeren hier
  const updateKlant = async (klant) => {
    if (klant.Postcode) {
      klant.Postcode = klant.Postcode.toString();
    }
    if (!klant.Telefoonnummer) klant.Telefoonnummer = '';
    createOrUpdateKlant(klant);
    setToevoegenFormulier(false);
  };

  if (error) return <ErrorDisplay errortext={error} provider="klanten" />;
  if (loading) return <div>Loading</div>;
  if (!klanten || (klanten && klanten.length === 0)) return <div>Loading</div>;

  return (
    <>
      <ControlGroup>
        <HTMLSelect
          options={['Zoeken:', 'Voornaam en Naam', 'Gemeente', 'Straat']}
          onChange={(e) => {
            if (e.currentTarget.value === 'Zoeken:') {
              setFilterKolommen(DEFAULTFILTERKOLOMMEN);
            } else {
              if (e.currentTarget.value === 'Voornaam en Naam') {
                setFilterKolommen(['Naam', 'Voornaam']);
              } else {
                setFilterKolommen([`${e.currentTarget.value}`]);
              }
            }
          }}
        />
        <InputGroup
          placeholder="zoeken"
          onChange={(e) => setFilter(e.currentTarget.value)}
        />

        <Checkbox
          checked={tableEditable}
          label="Wijzigen toegestaan"
          onChange={() => setTableEditable(!tableEditable)}
          style={{ marginTop: '5px', marginLeft: '5px' }}
        />
        <Button onClick={() => setToevoegenFormulier(true)}>Toevoegen</Button>
        {toevoegenFormulier && (
          <KlantToevoegen
            onClose={() => {
              setToevoegenFormulier(false);
            }}
            onSubmit={updateKlant}
          />
        )}
      </ControlGroup>

      <Spreadsheet
        data={klanten}
        kolommen={KOLOMMEN}
        onRowChange={updateKlant}
        filter={filter}
        filterKolommen={filterKolommen}
        kolombreedtes={KOLOMBREEDTES}
        tableEditable={tableEditable}
      />
    </>
  );
};

export default Klanten;
