import { axios } from '.';

export const getBestellingenByDag = async (datum) => {
  // datum komt hier binnen als UTC
  // wordt hier omgezet naar een jaar, maand en dag.
  const jaar = datum.getUTCFullYear();
  const maand = datum.getUTCMonth() + 1;
  const dag = datum.getUTCDate();
  const { data } = await axios.get(`bestellingen/${jaar}/${maand}/${dag}`);
  return data;
};

export const saveBestelling = async ({
  BestellingID,
  datum,
  klantID,
  gerechtID,
  aantal,
  betaald,
}) => {
  const { data } = await axios({
    method: 'BestellingID' ? 'put' : 'post',
    url: `bestellingen/${BestellingID ?? ''}`,
    data: {
      BestellingID,
      datum,
      klantID,
      gerechtID,
      aantal,
      betaald,
    },
  });
  return data;
};

