import {
  createContext,
  useState,
  useEffect,
  useCallback,
  useContext,
  useMemo,
} from 'react';
import * as klantenApi from '../api/klanten';
import { useSession } from './AuthProvider';

export const KlantenContext = createContext();
export const useKlanten = () => useContext(KlantenContext);

export const KlantenProvider = ({ children }) => {
  const { ready: authReady } = useSession();
  const [initialLoad, setInitialLoad] = useState(false);
  const [currentKlant, setCurrentKlant] = useState({});
  const [error, setError] = useState();
  const [loading, setLoading] = useState(false);
  const [klanten, setKlanten] = useState([]);
  const [errorType, setErrorType] = useState();

  const clearError = useCallback(async () => {
    setError(null);
    refreshKlanten();
  }, []);

  const refreshKlanten = useCallback(async () => {
    try {
      setError();
      setLoading(true);
      const data = await klantenApi.getAllKlanten();
      setKlanten(data.data);
    } catch (error) {
      console.log(error);
      setError(error);
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    if (authReady && !initialLoad) {
      refreshKlanten();
      setInitialLoad(true);
    }
  }, [authReady, initialLoad, refreshKlanten]);

  const createOrUpdateKlant = useCallback(
    async ({
      KlantID,
      Voornaam,
      Naam,
      Straat,
      Huisnummer,
      Postcode,
      Gemeente,
      Telefoonnummer,
      GSM,
      Email,
      OpenstaandSaldo,
      Vasteklant,
    }) => {
      setError();
      setLoading(true);
      try {
        const changedKlant = await klantenApi.saveKlant({
          KlantID,
          Voornaam,
          Naam,
          Straat,
          Huisnummer,
          Postcode,
          Gemeente,
          Telefoonnummer,
          GSM,
          Email,
          OpenstaandSaldo,
          Vasteklant,
        });
        await refreshKlanten();
        return changedKlant;
      } catch (error) {
        console.error(error);
        setError(error);
        throw error;
      } finally {
        setLoading(false);
      }
    },
    [refreshKlanten]
  );

  const value = useMemo(
    () => ({
      klanten,
      error,
      errorType,
      loading,
      currentKlant,
      setCurrentKlant,
      createOrUpdateKlant,
      clearError,
    }),
    [
      klanten,
      error,
      errorType,
      loading,
      setCurrentKlant,
      currentKlant,
      createOrUpdateKlant,
      clearError,
    ]
  );

  return (
    <KlantenContext.Provider value={value}>{children}</KlantenContext.Provider>
  );
};
