import { axios } from '.';

export const login = async (email, password) => {
  const { data } = await axios.post('gebruikers/login', { email, password });
  return data;
};

export const register = async ({ name, email, password }) => {
  const { data } = await axios.post('gebruikers/register', {
    name,
    email,
    password,
  });
  return data;
};

export const getUserById = async (id) => {
  const { data } = await axios.get(`gebruikers/${id}`);
  return data;
};
