import { KlantenContext } from '../contexts/KlantenProvider';
import { GerechtenContext } from '../contexts/GerechtenProvider';
import {
  FormGroup,
  ControlGroup,
  InputGroup,
  Button,
  MenuItem,
} from '@blueprintjs/core';
import { Suggest } from '@blueprintjs/select';
import { useContext, useEffect, useState } from 'react';

const BestellingenForm = (bestelling) => {
  const {
    klanten,
    error: bestellingError,
    loading: bestellingLoading,
  } = useContext(KlantenContext);
  const {
    gerechten,
    error: gerechtenError,
    loading: gerechtenLoading,
  } = useContext(GerechtenContext);

  let klantenData = [];
  let gerechtenData = [];

  const {
    bestellingID,
    datum,
    klantID,
    gerechtID,
    aantal,
    betaald,
    adres,
    gemeente,
    gerechtNaam,
    klantNaam,
  } = bestelling.bestelling;

  if (bestellingError || bestellingLoading) {
    klantenData = [];
  } else {
    klantenData = klanten;
  }
  if (gerechtenError || gerechtenLoading) {
    gerechtenData = [];
  } else {
    gerechtenData = gerechten;
  }

  const [geselecteerdeKlant, setGeselecteerdeKlant] = useState();
  const [geselecteerdGerecht, setGeselecteerdGerecht] = useState();

  useEffect(() => {
    setGeselecteerdeKlant(
      klanten.find((p) => klantNaam === `${p.Voornaam} ${p.Naam}`)
    );
  }, [klantNaam, klanten, bestelling]);

  useEffect(() => {
    setGeselecteerdGerecht(gerechten.find((p) => gerechtNaam === p.Naam));
  }, [gerechtNaam, bestelling, gerechten]);

  const displayKlant = (klant) => {
    return `${klant.Voornaam} ${klant.Naam}`;
  };
  const displayGerecht = (gerecht) => gerecht.Naam;

  const renderKlant = (klant, { handleClick, modifiers, query }) => {
    if (!modifiers.matchesPredicate) {
      return null;
    }
    const text = `${klant.Voornaam} ${klant.Naam}`;
    return (
      <MenuItem
        active={modifiers.active}
        disabled={modifiers.disabled}
        label={null}
        key={klant.klantID}
        onClick={handleClick}
        text={highlightText(text, query)}
      />
    );
  };

  const renderGerecht = (gerecht, { handleClick, modifiers, query }) => {
    if (!modifiers.matchesPredicate) {
      return null;
    }
    const text = gerecht.Naam;
    return (
      <MenuItem
        active={modifiers.active}
        disabled={modifiers.disabled}
        label={null}
        key={gerecht.gerechtID}
        onClick={handleClick}
        text={highlightText(text, query)}
      />
    );
  };

  const filterKlant = (query, klant) => {
    return (
      `${klant.Voornaam.toLowerCase()}`.indexOf(query.toLowerCase()) >= 0 ||
      `${klant.Naam.toLowerCase()}`.indexOf(query.toLowerCase()) >= 0
    );
  };

  const filterGerecht = (query, gerecht) =>
    gerecht.Naam.toLowerCase().startsWith(query.toLowerCase());

  function highlightText(text, query) {
    let lastIndex = 0;
    const words = query
      .split(/\s+/)
      .filter((word) => word.length > 0)
      .map(escapeRegExpChars);
    if (words.length === 0) {
      return [text];
    }
    const regexp = new RegExp(words.join('|'), 'gi');
    const tokens = [];
    while (true) {
      const match = regexp.exec(text);
      if (!match) {
        break;
      }
      const length = match[0].length;
      const before = text.slice(lastIndex, regexp.lastIndex - length);
      if (before.length > 0) {
        tokens.push(before);
      }
      lastIndex = regexp.lastIndex;
      tokens.push(<strong key={lastIndex}>{match[0]}</strong>);
    }
    const rest = text.slice(lastIndex);
    if (rest.length > 0) {
      tokens.push(rest);
    }
    return tokens;
  }

  function escapeRegExpChars(text) {
    return text.replace(/([.*+?^=!:${}()|\[\]\/\\])/g, '\\$1');
  }

  return (
    <>
      <Suggest
        items={klantenData}
        itemPredicate={filterKlant}
        inputValueRenderer={displayKlant}
        itemRenderer={renderKlant}
        allowCreate={false}
        // onItemSelect={handleFilmSelect}
        closeOnSelect={true}
        minimal={true}
        resetOnQuery={true}
        resetOnSelect={true}
        popoverProps={{ minimal: true }}
        selectedItem={geselecteerdeKlant}
        onItemSelect={(e) => setGeselecteerdeKlant(e)}
      />
      <Suggest
        items={gerechtenData}
        itemPredicate={filterGerecht}
        inputValueRenderer={displayGerecht}
        itemRenderer={renderGerecht}
        allowCreate={false}
        closeOnSelect={true}
        minimal={true}
        resetOnQuery={true}
        resetOnSelect={true}
        popoverProps={{ minimal: true }}
        selectedItem={geselecteerdGerecht}
        onItemSelect={(e) => setGeselecteerdGerecht(e)}
      />
      <FormGroup label="Adres" labelFor="text-input" labelInfo="(verplicht)">
        <ControlGroup>
          <InputGroup id="text-input" value={klantNaam} />
        </ControlGroup>
        <ControlGroup>
          <InputGroup id="text-input" value={gerechtNaam} />
        </ControlGroup>
        <ControlGroup>
          <InputGroup id="text-input" value={gemeente} />
        </ControlGroup>
        <ControlGroup>
          <InputGroup id="text-input" value={adres} />
        </ControlGroup>
        <ControlGroup>
          <InputGroup id="text-input" value={betaald} />
        </ControlGroup>
        <ControlGroup>
          <InputGroup id="text-input" value={gerechtID} />
        </ControlGroup>
        <ControlGroup>
          <InputGroup id="text-input" value={klantID} />
        </ControlGroup>
        <ControlGroup>
          <InputGroup id="text-input" value={aantal} />
        </ControlGroup>
        <ControlGroup>
          <InputGroup id="text-input" value={datum} />
        </ControlGroup>
        <ControlGroup>
          <InputGroup id="text-input" value={bestellingID} />
        </ControlGroup>
      </FormGroup>
    </>
  );
};

export default BestellingenForm;

